#navbar
{   
    display : flex;
    position : relative;
    align-items: center;
    
}

#navbar_stick
{
    position : fixed;
    top : 0px;
    width : 100%;
    z-index : 1;
}
#navbar::before
{
    content : "";
    background-color: black;
    position : absolute;
    top : 0px;
    left : 0px;
    height : 100%;
    width : 100%;
    z-index: -1;
    opacity : 0.4;
}

#logo
{
    margin : 0.125rem 0.125rem;
    /* margin : 2px 2px; */
    display : flex;
    align-items: center;
}

#logo img
{
    margin : 0.125rem 0.625rem;
    /* margin : 2px 10px; */
    border-radius: 50%;
    width : 10%;
}

#navbar ul 
{
    display : flex;
    position : absolute;
    left : 200px;
}
#navbar ul li
{
    list-style : none;
    font-weight: bolder;
    font-size: 1.5rem;
}

#navbar ul li a 
{
    display : block;
    padding : 0.125rem 1.25rem;
    /* padding : 2px 20px; */
    text-decoration: none;
    color : white;
    border-radius: 1.25rem;
    /* font: 2.5rem; */
    /* border-radius: 20px; */
}

#navbar ul li a:hover
{
    color : black;
    background-color: white;
}

#navbar button{
    border: none;
    outline: none;
    width: 8%;
    padding: 15px 0;
    color: #fff;
    font-size: 16px;
    letter-spacing: 1px;
    background: #603bbb;
    cursor: pointer;
    position: absolute;
    right: 20px;
}

#navbar button:hover
{
    background-color: #130141;
}

#home
{
    display : flex;
    flex-direction: column;
    /* padding: 5px 200px; */
    padding: 0.3125rem 12.5rem;
    justify-content : center;
    align-items : center;
    height : 400px;
    color : white;
}
#navbar_home
{
    position : relative;
}

#navbar_home::before
{
    content : "";
    background: url(../Images/AI-background.jpg) no-repeat center center/cover;
    position : absolute;
    top : 0px;
    left : 0px;
    height : 100%;
    width : 100%;
    z-index: -1;
    opacity : 0.8;
}
.primary_heading
{
    font-size: 2.8rem;
    padding : 0.75rem;
    /* padding : 12px; */
    /* font-family: 'Bree Serif', serif;  */
}

.secondary_heading
{
    font-size : 1.8rem;
    padding : 0.75rem;
    /* padding : 12px; */
}

p{
    font-size: 1.5rem;
}

#navbar_para{
    margin-top: 20px;
}

.center
{
    text-align: center;
}


#projects
{
    margin : 10px;
    display : flex;
    justify-content: center;
}

#projects .box
{
    border: 2px solid brown;
    padding : 25px;
    margin : 5px 30px 25px 30px;
    border-radius : 20px;
    background-color: white;
    transition : all 0.3s ease-in-out;
}
#project1
{
    width : 60%;
    height : 80%;
}
#project2
{
    width : 40%;
    height : 80%;
}

#projects .box:hover
{
    color: white;
    background-color: #f77031;
    cursor : pointer;
    transform: scale(1.05);
    z-index  : 2;
}

#projects .box img
{
    display: block;
    width: 250px;
    margin : auto;
}

.btn
{
    padding: 5px 20px;
    border: 2px solid white;
    background-color : brown;
    color : white;
    margin : 10px 5px;
    border-radius: 10px;
    cursor : pointer;
    transition : all;
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out;
}
.btn:hover
{
    background-color : rgb(94, 34, 34);
}

#contact_us_image
{
    position : relative;
}
#contact_us
{
    height : auto;
}
#contact_us::before
{
    content: " ";
    position : absolute;
    top : 0px;
    left : 0px;
    background: url('../Images/contact_us_background.jpg') no-repeat center center/cover;
    height : 100%;
    width : 100%;
    z-index : -1;
    opacity : 0.5;
}
#contact_us form 
{
    width : 30%;
}
#contact
{
    display : flex;
    flex-direction : column;
    align-items : center;
    justify-content : center;
    padding-bottom: 10px;
}

#contact input, #contact textarea
{
    width : 100%;
    /* text-align: center; */
    padding : 5px 10px;
    border-radius : 10px;
    font-size : 1.1rem;
}
 #contact label
{
    font-size: 1.2rem;
    font-weight: bolder;
}

